import React, { useState } from "react";
import Axios from "axios";

import { ModalError } from "../modalError/ModalError";

import "./createAtaque.css";

const CreateAtaque = ({ history }) => {
  const [body, setBody] = useState({ nombre: "", daño: 0, tipo: "" });
  const [error, setError] = useState({});

  const handleChange = e => {
    setBody({ ...body, [e.target.name]: e.target.value });
  };

  const handleOpen = () => {
    setError({});
  };

  const handleSubmit = () => {
    Axios.post("http://localhost:5000/api/ataques", body)
      .then(res => res.data)
      .then(ataque => {
        history.push(`/`);
      })
      .catch(setError);
  };

  const { nombre, daño, tipo } = body;
  return (
    <>
      <h1 className="pokemonTitle">Crear Ataque</h1>
      <div className="inputsContainer">
        <input
          name="nombre"
          className="inputsPokemon"
          placeholder="Nombre"
          onChange={handleChange}
          value={nombre}
          type="text"
        />
        <input
          name="daño"
          className="inputsPokemon"
          placeholder="Daño"
          onChange={handleChange}
          value={daño}
          type="number"
        />
        <input
          name="tipo"
          className="inputsPokemon"
          placeholder="Tipo"
          onChange={handleChange}
          value={tipo}
          type="text"
        />
      </div>
      <button onClick={handleSubmit} className="buttons">
        Crear
      </button>
      {error.response ? (
        <ModalError
          status={error.response.status}
          message={error.response.data.message || error.response.data}
          handleOpen={handleOpen}
        />
      ) : null}
    </>
  );
};

export default CreateAtaque;
