import React, { useState, useEffect } from "react";
import Axios from "axios";

import { ModalError } from "../modalError/ModalError";

import "./createPokemon.css";

const CreatePokemon = ({ history }) => {
  const [body, setBody] = useState({
    nombre: "",
    level: 0,
    tipo: "",
    ataques: [],
    imgURI: ""
  });
  const [allAttacks, setAllAttacks] = useState({});
  const [error, setError] = useState({});

  const handleChange = e => {
    setBody({ ...body, [e.target.name]: e.target.value });
  };

  const handleOpen = () => {
    setError({});
  };

  const handleSubmit = () => {
    Axios.post("http://localhost:5000/api/pokemons", body)
      .then(res => res.data)
      .then(pokemon => {
        history.push(`/pokemon/${pokemon._id}`);
      })
      .catch(setError);
  };

  const isSelected = id => {
    if (ataques.length) {
      for (let i = 0; i < ataques.length; i++) {
        if (ataques[i] === id) return "cardAttackActivate";
      }
    }
    return "";
  };

  const handleSelect = attack => {
    let newAttacks = [];
    const verifyExist = id => {
      for (let i = 0; i < ataques.length; i++) {
        if (ataques[i]._id === id) return true;
      }
      return false;
    };
    if (verifyExist(attack._id)) {
      newAttacks = ataques.filter(e => e._id !== attack._id);
    } else {
      newAttacks = [...ataques, attack];
    }
    console.log(newAttacks);
    setBody({ ...body, ataques: newAttacks });
  };

  const { nombre, level, tipo, ataques, imgURI } = body;

  useEffect(() => {
    Axios.get("http://localhost:5000/api/ataques")
      .then(res => res.data)
      .then(setAllAttacks)
      .catch(setError);
  }, []);

  return (
    <>
      <h1 className="title">Crear Pokemon</h1>
      <img alt="" src={imgURI} className="createImage" />
      <div className="inputsContainer">
        <input
          name="imgURI"
          placeholder="Url de la Imagen"
          onChange={handleChange}
          className="inputsPokemon"
          value={imgURI}
          type="text"
        />
        <input
          name="nombre"
          placeholder="Nombre"
          onChange={handleChange}
          className="inputsPokemon"
          value={nombre}
          type="text"
        />
        <input
          name="level"
          placeholder="Nivel"
          onChange={handleChange}
          className="inputsPokemon"
          value={level}
          type="number"
        />
        <input
          name="tipo"
          placeholder="Tipo"
          onChange={handleChange}
          className="inputsPokemon"
          value={tipo}
          type="text"
        />
      </div>
      {allAttacks.length ? (
        <div className="ataquesContainer">
          {allAttacks.map(attack => (
            <div
              className={`cardAttack ${isSelected(attack)}`}
              key={attack._id}
              onClick={() => handleSelect(attack)}
            >
              {attack.nombre}
            </div>
          ))}
        </div>
      ) : null}
      <div className="buttonContainer">
        <button className="buttons" onClick={handleSubmit}>
          Crear
        </button>
      </div>
      {error.response ? (
        <ModalError
          status={error.response.status}
          message={error.response.data.message || error.response.data}
          handleOpen={handleOpen}
        />
      ) : null}
    </>
  );
};

export default CreatePokemon;
