import React from "react";
import { Switch, Route } from "react-router-dom";
import "./App.css";

import Pokemones from "./components/pokemones/Pokemones";
import Pokemon from "./components/pokemon/Pokemon";
import PokemonEdit from "./components/pokemonEdit/PokemonEdit";
import Navbar from "./components/navbar/Navbar";
import CreatePokemon from "./components/createPokemon/CreatePokemon";
import CreateAtaque from "./components/createAtaque/CreateAtaque";

function App() {
  return (
    <div className="App App-header">
      <Navbar />
      <Switch>
        <Route
          exact
          path="/pokemon/:id/edit"
          render={({ match, history, location }) => (
            <PokemonEdit
              id={match.params.id}
              history={history}
              location={location}
            />
          )}
        />
        <Route
          exact
          path="/pokemon/create"
          render={({ history }) => <CreatePokemon history={history} />}
        />
        <Route
          exact
          path="/pokemon/:id"
          render={({ match, location }) => (
            <Pokemon id={match.params.id} location={location} />
          )}
        />
        <Route
          path="/ataques/crear"
          render={({ history }) => <CreateAtaque history={history} />}
        />
        <Route
          path="/pokemones"
          render={({ history }) => <Pokemones history={history} />}
        />
        <Route
          path="/"
          render={({ history }) => <Pokemones history={history} />}
        />
      </Switch>
    </div>
  );
}

export default App;
