import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Axios from "axios";
import { ModalError } from "../modalError/ModalError";
import "./pokemon.css";
const Pokemon = ({ id, location, history }) => {
  const [pokemon, setPokemon] = useState({});
  const [error, setError] = useState({});

  const handleOpen = () => {
    setError({});
  };

  useEffect(() => {
    Axios.get(`http://localhost:5000/api/pokemons/${id}`)
      .then(res => {
        return res.data;
      })
      .then(setPokemon)
      .catch(setError);
  }, [id]);

  const handleDelete = id => {
    Axios.delete(`http://localhost:5000/api/pokemons/${id}`)
      .then(() => () => {
        history.push("/");
      })
      .catch(setError);
  };

  return (
    <div className="pokemonContainer">
      <img alt={pokemon._id} src={pokemon.imgURI} className="pokemonImg" />
      <div className="pokomenName">Nombre : {pokemon.nombre}</div>
      <div className="pokomenLevel">Nivel : {pokemon.level}</div>
      <div className="pokomenType">Tipo : {pokemon.tipo}</div>
      <div className="cardAttacks">
        Ataques
        <ul>
          {pokemon.ataques &&
            pokemon.ataques.map(attack => (
              <li key={attack._id} className="pokemonAttacks">
                {attack.nombre}
              </li>
            ))}
        </ul>
      </div>
      {error.response ? (
        <ModalError
          status={error.response.status}
          message={error.response.data}
          handleOpen={handleOpen}
        />
      ) : null}
      <div className="buttonContainer">
        <Link to={`${location && location.pathname}/edit`}>
          <button className="buttons">Editar</button>
        </Link>
        <button className="buttons" onClick={() => handleDelete(id)}>
          Borrar
        </button>
      </div>
    </div>
  );
};

export default Pokemon;
