import React from "react";
import "./pokemonCard.css";
const PokemonCard = ({
  nombre,
  _id,
  tipo,
  level,
  imgURI,
  history,
  attacks
}) => {
  const onClick = () => {
    history.push(`/pokemon/${_id}`);
  };

  return (
    <div className="cardContainer" key={_id} onClick={onClick}>
      <img alt={_id} src={imgURI} className="cardImg" />
      <div className="cardTitle">Nombre: {nombre}</div>
      <div className="cardLevel">Nivel: {level}</div>
      <div className="cardType">Tipo: {tipo}</div>
      <div className="cardAttacks">
        <ul>{attacks && attacks.map(attack => <li>{attack.nombre}</li>)}</ul>
      </div>
    </div>
  );
};

export default PokemonCard;
