import React from "react";
import "./modalError.css";
export const ModalError = ({ status, message, handleOpen }) => {
  return (
    <div className="containerAbsolute" onClick={handleOpen}>
      <div className="content" onClick={e => e.stopPropagation()}>
        <div className="errorTitle">Hubo un ERROR!</div>
        <div className="errorStatus">
          El codigo del error fue:
          <div className="errorMessage">{status}</div>
        </div>
        <div className="errorStatus">
          El mensaje del error fue:
          <div className="errorMessage">" {message} "</div>
        </div>
      </div>
    </div>
  );
};
