import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import "./pokemones.css";
import Axios from "axios";
import { ModalError } from "../modalError/ModalError";
import PokemonCard from "../pokemonCard/PokemonCard";
const Pokemones = ({ nombre, history }) => {
  const [pokemones, setPokemones] = useState([]);
  const [error, setError] = useState({});

  useEffect(() => {
    Axios.get("http://localhost:5000/api/pokemons")
      .then(res => res.data)
      .then(pokemons => {
        setPokemones(pokemons);
      })
      .catch(err => setError(err));
  }, [pokemones.length]);

  const handleOpen = () => {
    setError({});
  };
  return (
    <div>
      <div className="pokemonsTitle">hola</div>
      <div className="pokemonsSubtitle">Estos son los pokemones</div>
      <Link to="/pokemon/create">
        <button className="buttons">Agregar Pokemon</button>
      </Link>
      <div className="pokemonsContainer">
        {pokemones.length ? (
          pokemones.map(pokemon => {
            return (
              <PokemonCard {...pokemon} history={history} key={pokemon._id} />
            );
          })
        ) : (
          <h3 className="noPokemons">No hay Pokemones aún</h3>
        )}
      </div>
      {error.response ? (
        <ModalError
          status={error.response.status}
          message={error.response.data}
          handleOpen={handleOpen}
        />
      ) : null}
    </div>
  );
};

export default Pokemones;
