import React, { useState, useEffect } from "react";
import { ModalError } from "../modalError/ModalError";
import Axios from "axios";
import "./pokemonEdit.css";

const PokemonEdit = ({ id, history, location }) => {
  const [body, setBody] = useState({
    nombre: "",
    level: 0,
    tipo: "",
    ataques: [],
    imgURI: ""
  });
  const [error, setError] = useState({});
  const [allAttacks, setAllAttacks] = useState({});

  const handleChange = e => {
    setBody({ ...body, [e.target.name]: e.target.value });
  };

  const handleSelect = attack => {
    let newAttacks = [];
    const verifyExist = id => {
      for (let i = 0; i < ataques.length; i++) {
        if (ataques[i]._id === id) return true;
      }
      return false;
    };
    if (verifyExist(attack._id)) {
      newAttacks = ataques.filter(e => e._id !== attack._id);
    } else {
      newAttacks = [...ataques, attack];
    }
    setBody({ ...body, ataques: newAttacks });
  };

  const isSelected = id => {
    if (ataques.length) {
      for (let i = 0; i < ataques.length; i++) {
        if (ataques[i]._id === id) return "cardAttackActivate";
      }
    }
    return "";
  };

  const handleOpen = () => {
    setError({});
  };

  useEffect(() => {
    Axios.get(`http://localhost:5000/api/pokemons/${id}`)
      .then(res => res.data)
      .then(setBody)
      .catch(setError);
  }, [id]);

  useEffect(() => {
    Axios.get("http://localhost:5000/api/ataques")
      .then(res => res.data)
      .then(setAllAttacks)
      .catch(setError);
  }, []);

  const handleSubmit = () => {
    Axios.put(`http://localhost:5000/api/pokemons/${id}`, body)
      .then(res => res.data)
      .then(pokemonUpdated => {
        history.push(`/pokemon/${pokemonUpdated._id}`);
      })
      .catch(setError);
  };
  const { imgURI, level, tipo, nombre, ataques } = body;
  return (
    <div className="editContainer">
      <img alt="" src={imgURI} className="pokemonImg" />
      <div className="inputsContainer">
        <input
          name="imgURI"
          value={imgURI}
          type="text"
          placeholder="Url de la imagen"
          className="inputsPokemon"
          onChange={handleChange}
        />
        <input
          name="nombre"
          value={nombre}
          type="text"
          placeholder="Nombre"
          className="inputsPokemon"
          onChange={handleChange}
        />
        <input
          name="tipo"
          value={tipo}
          type="text"
          placeholder="Tipo"
          className="inputsPokemon"
          onChange={handleChange}
        />
        <input
          name="level"
          value={level}
          type="number"
          placeholder="Nivel"
          className="inputsPokemon"
          onChange={handleChange}
        />
      </div>
      {allAttacks.length ? (
        <div className="ataquesContainer">
          {allAttacks.map(attack => (
            <div
              className={`cardAttack ${isSelected(attack._id)}`}
              key={attack._id}
              onClick={() => handleSelect(attack)}
            >
              {attack.nombre}
            </div>
          ))}
        </div>
      ) : null}
      <button className="buttons" onClick={handleSubmit}>
        Actualizar
      </button>
      {error.response ? (
        <ModalError
          status={error.response.status}
          message={error.response.data}
          handleOpen={handleOpen}
        />
      ) : null}
    </div>
  );
};

export default PokemonEdit;
